.stories {
    padding: 150px 0 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #1C1C1D;

    &__title {
        text-align: center;
        line-height: 1.06666666667em; // 48px
        margin-bottom: 60px;

    }

    &__items {
        display: flex;
        justify-content: space-between;
        gap: 5%;
    }

    &__item {
        flex: 1;
    }

    &__item-img {
        width: 100%;
        object-fit: contain;
    }

    &__item-name {
        font-weight: bold;
        font-size: 1.5em; // 24px
        line-height: 1.33333333333em; // 32px
        letter-spacing: 0.625px;
        color: #1C1C1D;
    }

    &__item-text {
        line-height: 1.5em; // 27px
        margin: 5px 0px 15px;
    }

    &__item-date {
        font-weight: 500;
        font-size: 1.125em; // 18px
        line-height: 26px;
        letter-spacing: 0.5px;
        color: #1C1C1D;
    }
}

@media (max-width: 600px) {
    .stories {
        padding: 60px 0;
        &__items {
            flex-direction: column;
            align-items: center;
            gap: 0;
        }

        &__item {
            max-width: 380px;
            margin-bottom: 60px;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

