.authorization {
    padding: 80px 0;
    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__content {
        max-width: 400px;
        
    }

    &__title {
    }

    &__text {
        margin-top: 1.38888888889em;
        margin-bottom: 2.77777777778em; // 50px
    }

    &__container-btn {
        display: flex;

    }

    &__btn {
        background-color: #FA6648;
        color: #fff;
        transition: 0.25s;
        min-width: none;
        width: 100%;
        &:hover {
            background-color: #1BDBC4;
        }
    }

    &__form {
        display: flex;
        flex-direction: column;
        border: 2px solid #1C1C1D;
        border-radius: 10px;
        border-bottom: 10px solid #1C1C1D;
        padding: 40px;
        width: 100%;
        max-width: 500px;
    }

    &__list {
        padding: 0;
        list-style: none;
        display: flex;
        gap: 19px;
        margin-bottom: 40px;
    }

    &__list-item {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: bold;
        font-size: 1.5rem;
        line-height: 133%;
        letter-spacing: 0.625px;
        cursor: pointer;

        &--active {
            color: #FA6648;
        }
    }

    &__items {
        
    }

    &__item {
        display: flex;
        flex-direction: column;
    }

    &__form-input {
        margin-bottom: 15px;

        &[type=checkbox] {
            opacity: 0;
            margin: 0;
            width: 0;
            height: 0;
            // height: 32px;
            // width: 32px;
            position: absolute;
            left: 0;
            top: -3px;
            border-radius: 8px;

            &:checked {

            }
        }
    }

    &__label {
        position: relative;
        font-family: "Montserrat";
        font-style: normal;
        font-weight: 500;
        font-size: 1.125rem;
        line-height: 144%;
        letter-spacing: 0.5px;
        color: #474A57;
        margin-top: 0.55555555555em; // 10px
        margin-bottom: 1.83333333333em; // 33px
        padding-left: 47px;
        cursor: pointer;

        &::before {
            position: absolute;
            content: '';
            display: inline-block;
            width: 32px;
            height: 32px;
            background-color: #FFBD12;
            border: 2px solid #1C1C1D;
            border-radius: 8px;
            left: 0;
            top: -3px;
            transition: 0.25s;

            // click
            background-image: url('../../img/icons/checked.svg');
            background-position: center;
            background-repeat: no-repeat;

        }

        &:hover::before {
            background-color: #f72d2d;
            border: 2px solid black;
        }

    }

    &__button {
        display: flex;
        justify-content: center;
        border: 1px solid #1C1C11;
        border: bottom 3px solid #1C1C11;
        border-radius: 10px;
        background-color: #FA6648;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 900;
        font-size: 1rem;
        line-height: 100%;
        text-align: center;
        padding: 17px;
        text-align: center;
        color: #fff;
        transition: 0.25s;
        &:hover {
            background-color: #1BDBC4;
        }
        
    }    
}

@media (max-width: 1340px) {
    .authorization {
        &__form {
            max-width: 400px;
        }
    }
}

@media (max-width: 1100px) {
    .authorization {
        &__form {
            padding: 20px;
        }
        &__list {
            margin-bottom: 30px;
        }
    }
}

@media (max-width: 1050px) {
    .authorization {
        &__row {
            flex-direction: column;
        }

        &__content {
            width: 70%;
            max-width: none;
            text-align: center;
            margin-bottom: 60px;
        }

        &__container-btn {
            display: flex;
            justify-content: center;
        }

        &__form {
            padding: 40px;
            max-width: 500px;
        }
    }
}
@media (max-width: 553px) {
    .authorization {
        padding: 40px 0;
        &__content {
            width: 90%;
        }
    }
}

@media (max-width: 500px) {
    .authorization {
        &__row {
            flex-direction: column;
        }

        &__content {
            width: 100%;
            max-width: none;
            text-align: center;
            margin-bottom: 60px;
        }

        &__container-btn {
            display: flex;
            justify-content: center;
        }

        &__form {
            padding: 20px;
            max-width: 500px;
        }
    }
}

@media (max-width: 410px) {
    .authorization {
        &__btn {
            margin-right: 0;
            margin-bottom: 15px;

            &:last-child {
                margin-bottom: 0;
            }
        }
        &__container-btn {
            flex-direction: column;
            align-items: center;
        }
    }
}

@media (max-width: 410px) {
    .authorization {
        &__form {
            padding: 10px;
        }
    }
}