.tarif-plan {
    padding: 80px 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    &__descr {
        display: block;

        text-align: center;
        font-weight: 500;
        font-size: 1.125rem; // 18px
        line-height: 144%; // 26px
        text-align: center;
        letter-spacing: 0.5px;
        color: #474A57;
    }

    &__title {
        text-align: center;
        font-weight: normal;
        line-height: 108%; // 48px
        text-align: center;
        letter-spacing: 2px;
        color: #1C1C1D;
        margin: 0.3333333em 0 1.3333333em; // 15px 0 60px

    }

    &__category-list {
        z-index: 1;
        text-align: center;
        list-style: none;
        justify-content: center;
        display: flex;
        padding: 0;
        margin-bottom: 50px;
        cursor: pointer;

    }

    &__category-list-item {
        position: relative;
        flex: 1;
        max-width: 160px;
        border: 2px solid #1C1C1D;
        border-bottom: 4px solid #1C1C1D;
        border-top-left-radius: 25px;
        border-bottom-left-radius: 25px;
        padding: 12px 35px 12px 35px;
        margin: 0 -1px;
        background-color: #fff;

        font-weight: bold;
        font-size: 1.25rem; // 20px
        line-height: 160%;
        text-align: center;
        letter-spacing: 0.625px;

        /* Main / Black */

        color: #1C1C1D;

        &::after {
            transition: height 2s;
            content: '';
            position: absolute;
            display: none;
            width: 3px;
            height: 4px;
            top: calc(50% - 2px);
            left: 30%;
            color: black;
            background-color: black;

            z-index: -1;

        }

        &::before {
            content: '';
            position: absolute;
            display: none;
            width: 3px;
            height: 2px;
            top: calc(50% - 2px);
            right: 30%;
            color: black;
            background-color: black;

            z-index: -1;
            transition: height 2s;

        }

        &:hover::after {
            display: block;
            height: 100px;
            top: calc(50% - 50px);
            transition: height 2s;
        }

        &:hover::before {
            display: block;
            height: 100px;
            top: calc(50% - 50px);
            transition: height 2s;
        }

        &:last-child {
            border-top-left-radius: 0px;
            border-bottom-left-radius: 0px;
            border-end-end-radius: 25px;
            border-start-end-radius: 25px;
            
        }
    }

    &__items {

    }

    &__item {
        z-index: 1;
        position: relative;
        padding: 30px 30px 85px;
        border: 2px solid #1C1C1D;
        box-shadow: 0px 8px 0px #202020;
        border-radius: 10px;

        &:nth-child(4n+1) {
            background-color: #F95A2C;
        }

        &:nth-child(4n+2) {
            background-color: #1BDBC4;
        }

        &:nth-child(4n+3) {
            background-color: #962EFF;
        }

        &:nth-child(4n) {
            background-color: #FFBD12;
        }
    }

    &__item-category, &__characteristic-item {
        font-weight: 500;
        font-size: 1.125rem; // 18px
        line-height: 144%;
        letter-spacing: 0.5px;
        color: #1C1C1D;
        margin-bottom: 0.55555555555em; //10px
    }

    &__duration {
        margin:  0.66666666em 0 1em; // 20px 0 30px
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 600;
        font-size: 1.875rem; // 45px
        line-height: 150%;
        letter-spacing: 0.45px;
        color: #1C1C1D;
    }

    &__characteristic-list {
        padding: 0;
        list-style: none;
    
    }

    &__btn {
        position: absolute;
        bottom: 40px;
        left: 30px;
    }

    &__months {
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(270px, 1fr));
        gap: 30px;
    }

    &__year {
        display: none;
    }
}

@media (max-width: 500px) {
    .tarif-plan {
        &__item {
            padding: 20px 20px 70px;
        }

        &__btn {
            bottom: 30px;
            left: 20px;
        }

        &__months {
            grid-template-columns: 1fr;
        }
    }
}

@media (max-width: 400px) {
    .tarif-plan {
        &__category-list-item {

            border-top-left-radius: 25px;
            border-bottom-left-radius: 25px;
            padding: 7px 20px 7px 20px;
    
            font-size: 1.25rem; // 20px
    
            &:last-child {
                border-top-left-radius: 0px;
                border-bottom-left-radius: 0px;
                border-end-end-radius: 25px;
                border-start-end-radius: 25px;
                
            }
        }
    }
}

@media (max-width: 320px) {
    .tarif-plan {
        &__item-category, &__characteristic-item {
            font-size: 1rem; // 16px
        }

        &__category-list-item {
            font-size: 1.125rem; // 18px
        }

        &__duration {
            font-size: 1.38888888889rem; // 25px
        }

        &__btn {
            min-width: none;
            max-width: none;
            width: calc(100% - 40px);
            font-size: 1rem; // 16px
        }
    }
}