.tracker {
    display: flex;
    position: relative;
    flex-direction: column;
    overflow: hidden;
    min-height: 100vh;
    background-color: #7711DD;
    padding: 120px 0 60px 0;

    &::after {
        display: block;
        position: absolute;
        content: '';
        background-image: url('../../img/tracker/tracker-app.png');
        background-repeat: no-repeat;
        background-size: contain;
        background-position: bottom;
        width: calc(40vh + 22%);
        height: 90%;
        right: 0;
        bottom: 0;
    }
    
    &__content {
        padding-top: calc(30vh - 120px);
        display: flex;
        flex-direction: column;
    }
    &__title {
        max-width: 590px;
        font-size: 4.375rem; // 70px
        color: #FFFFFF;
        line-height: 107%; // 75px
        letter-spacing: 2px;
        margin-bottom: 0.42857142857em; // 30px 

    }

    &__text {
        max-width: 420px;
        color: #FFFFFF;
        margin-bottom: 2.77777777778em; // 50px
    }

    &__btn {   
        &:hover {
            background-color: #1BDBC4;
        }
    }
}

@media (max-width: 1400px) {
    .tracker {
        &::after {
            right: calc(30vw - 600px);
            width: 700px;
        }

    }
}

@media (max-width: 1100px) {
    .tracker {
        &::after {
            width: 600px;
        }

    }
}

@media (max-width: 950px) {
    .tracker {
        &::after {
            content: none;
        }

    }
}

@media (max-width: 700px) {
    .tracker {
        &__title {
            font-size: 3.75rem; //60px
    
        }
    }
}

@media (max-width: 600px) {
    .tracker {
        &__title {
            font-size: 3.125rem; //50px
        }
    }
}

@media (max-width: 500px) {
    .tracker {
        &__title {
            text-align: center;
        }
    
        &__text {
           text-align: center;
        }
    
        &__btn-container {
            justify-content: space-around;
        }
        
        &__btn {
            margin-right: 0;
        }
    }
}

@media (max-width: 400px) {
    .tracker {
        &__btn-container {
            flex-direction: column;
            align-items: center;
        }

        &__btn {
            margin-bottom: 15px;
            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}

@media (max-height: 400px) {
    .tracker {
        &__content {
            padding-top: 0;
        }
    }
}