.header {
    // .header_list
    position: absolute;
    width: 100%;
    top: 0;
    z-index: 1;
   // position: relative;
    padding: 19px 0;

    &__list {
        display: flex;
        padding: 0px;
        margin-right: 55px;
        list-style: none;
    }

    &__menu {
        display: flex;
        align-items: center;
    }

    // .header__row

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    // .header__logo

    &__logo {
        display: block;
        object-fit: contain;
    }

    // .header__navigation

    &__navigation {
        display: flex;
        align-items: center;
    }

    // .header__list-item

    &__list-item {
        margin-right: 31px;
        &:last-child {
            margin-right: 0;
        }
    }

    // .header__link

    &__link {
        font-weight: 900;
        font-size: 1.25rem; // 20px
        color: #fff;
        text-decoration: none;
        transition: 0.2s;

        &:hover {
            color: #1BDBC4;
        }

    }
}
.container {
}
.burger {
    position: relative;
    min-width: 50px;
    min-height: 50px;
    z-index: 9;
    // .burger__img

    &__img {
        display: block;
    }

    &__line {
        position: absolute;
        width: 22px;
        height: 2px;
        background-color: #1C1C1D;
        border-radius: 2.5px;
        top: 0;
        right: calc(50% - 11px);
        transition-duration: 0.3s;

        &:nth-of-type(1) {
            top: calc(46% - 7px);
        }
        &:nth-of-type(2) {
            top: 46%; // center
        }
        &:nth-of-type(3) {
            top: calc(46% + 7px);
        }
    }

    &--active {
        
        .burger__line {
            &:nth-of-type(1) {
                top: calc(46% - 7px + 7px); 
                transform: rotate(45deg);
            }
            &:nth-of-type(2) {
                display: none;
            }
            &:nth-of-type(3) {
                top : calc(46% + 7px - 7px);
                transform: rotate(-45deg);
            }   
        }

    }

    &__list {
        display: none;
    }

    // .burger__item

    &__item {
    }

    // .burger__link

    &__link {
    }
}


.navigation {
    display: none; // flex
    justify-content: center;
    align-items: center;
    height: 300px;
    background-color: #FA6648;
    position: absolute;
    width: 100%;
    top: 100%;
    right: 0;
    z-index: 10;

    &__list {
        list-style: none;
        padding: 0;
        text-align: center;
    }

    &__list-item {
        margin-bottom: 20px;

        &:last-child {
            margin-bottom: 0px;
        }
    }

    &__link {
        font-weight: 500;
        font-size: 1.25rem; // 20px
        color: #fff;
        text-decoration: none;
        transition: 0.2s;
    }
}

@media (max-width: 400px) {
    .header {
        &__logo {
            height: 45px;
        }
    }
}
@media (max-width: 350px) {
    .header {
        &__list {
            margin-right: 30px;
        }
        &__list-item {
            margin-right: 20px;
        }
    }
}