.footer {
    padding: 42px 0;
    background-color: #7711DD;
    position: relative;
    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__logo {
        object-fit: contain;
    }

    &__list {
        padding: 0;
        margin: 0;
        display: flex;
        list-style: none;
    }

    &__list-item {
        margin-right: 34px;

        &:last-child {
            margin-right: 0;
        }
    }

    &__link {
        text-decoration: none;
        font-family: "Montserrat";
        font-style: normal;
        font-weight: bold;
        color: #FFFFFF;
        font-size: 1.0625rem; // 17px
        line-height: 123%; // 21px
        letter-spacing: 0.5px;
        transition: 0.2s;

        &:hover {
            color: #1BDBC4;
        }
    }

    &__btn-container {
        margin-top: 0;
    }

    &__btn {
        background-color: #FFBD12;
        color: #fff;
        transition: 0.25s;
        &:hover {
            background-color: #1BDBC4;
        }
    }
}



@media (max-width: 900px) {
    .footer {
        &__logo {
            max-height: 40px;
        }
        &__link {
            font-size: 1rem;
        }
    }
}
@media (max-width: 760px) {
    .footer {
        &__logo {
            position: absolute;
            top: 15px;
            left: 15px;
        }
        &__list {
            justify-content: space-evenly;
            width: 100%;
            margin-bottom: 30px;
        }
        &__btn-container {
            width: 100%;
            justify-content: space-around;
        }
        &__row {
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}

@media (max-width: 470px) {
    .footer {
        padding: 30px 0;
        &__list {
            justify-content: center;
        }
        &__btn-container {
            flex-direction: column;
            align-items: center;
        }
        &__btn {
            margin-right: 0;
            margin-bottom: 10px;
            max-width: 180px;
        }
    }
}
@media (max-width: 410px) {
    .footer {
        &__list {
            flex-direction: column;
            align-items: center;
            gap: 0;

        }
        &__list-item {
            margin: 0;
            margin-bottom: 7px;
        }
    }
}
