.btn-container {
    display: flex;
    z-index: 1;
}
.btn {
    position: relative;
    border: 1px solid #1C1C1D;
    border-bottom: 3px solid #1C1C1D;
    border-radius: 10px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 189px;
    padding: 10px 31px;
    transition: transform 2s;

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 900;
    font-size: 1rem; // 16px
    line-height: 1em; // 16px
    letter-spacing: 0.45px;

    color: #1C1C1D;

    margin-right: 15px;
    cursor: pointer;
    &:last-child {
        margin-right: 0;
    }

    &::after {
        position: absolute;
        display: none;
        align-items: center;
        justify-content: center;
        border: 1px solid #1C1C1D;
        font-weight: 600;
        z-index: -1;
        content: 'click';
        width: 50px;
        height: 20px;
        top: -20px;
        background-color: #fff;
        color: #1C1C1D;
        right: 10%;
    }
    &:hover {
        &::after {
            display: flex;
        } 
    }

}

.ios-black {
    &::before {
        display: block;
        margin-right: 9px;
        content: '';
        background-image: url('../../img/icons/ios-download.svg');
        background-size: contain;
        width: 25px;
        height: 25px;
    }
}

.android-black {
    &::before {
        display: block;
        margin-right: 9px;
        content: '';
        background-image: url('../../img/icons/android-download.svg');
        background-size: contain;
        width: 25px;
        height: 25px;
    }
}

.ios-white {
    &::before {
        display: block;
        margin-right: 9px;
        content: '';
        background-image: url('../../img/icons/ios-download-white.svg');
        background-size: contain;
        width: 25px;
        height: 25px;
    }
}

.android-white {
    &::before {
        display: block;
        margin-right: 9px;
        content: '';
        background-image: url('../../img/icons/android-download-white.svg');
        background-size: contain;
        width: 25px;
        height: 25px;
    }
}

@media (max-width: 900px) {
    .btn {
        padding: 9px 17px;
    }
}


@media (max-width: 760px) {
    .btn {
        &::after {
            content: none;
        }
    }
}
