.input {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 1.125rem; // 18px
    line-height: 1.44444444444em; // 26px
    letter-spacing: 0.5px;
    padding: 0.66666666666em 1.11111111111em; // 12px 20px
    border: 1px solid #1C1C1D;
    border-bottom: 3px solid #1C1C1D;
    border-radius: 10px;

    color: #1C1C1D;
}
