.about {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFFFFF;
    padding: 80px 0;

    &__title {
        margin: 0 auto;
        text-align: center;
        letter-spacing: 2px;
        color: #1C1C1D;
        max-width: 700px;
        margin-bottom: 0.66666666666em; // 30px
    }

    &__text {
        margin: 0 auto;
        text-align: center;
        max-width: 501px;
        margin-bottom: 3.33333333333em; // 60px

    }

    &__slider {
    }
}

.slider {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    &__display {
        position: relative;
        overflow: hidden;
        width: 100%;
        max-width: 100%;
        
        // // js change this
        height: 181px;
    }

    &__row {
        position: absolute;
        display: flex;
        top: 0;
        gap: 30px;

        // js change this
        width: 4410px;
        left: -50%;
    }

    &__item {
        background-color: #FA6648;
        border: 2px solid #1C1C1D;
        border-bottom: 6px solid #1C1C1D;
        border-radius: 10px;

        padding: 25px 20px 30px;
        max-width: 340px;
        max-height: 181px;

        &:nth-child(3n + 1) {
            background-color: #FFBD12;
        }

        &:nth-child(3n + 2) {
            background-color: #FA6648;
        }

        &:nth-child(3n) {
            background-color: #FFB0BC;
        }
    }

    &__item-top {
        display: flex;
        justify-content: space-between;
    }

    &__item-img {
        margin-right: 22px;
    }

    &__item-descr {
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: bold;
        font-size: 1.25rem; // 20px
        line-height: 1.2em; // 24px
        letter-spacing: 0.5px;
        color: #FFFFFF;

        max-width: 173px;
    }

    &__item-text {
        font-style: normal;
        font-weight: normal;
        font-size: 1rem; // 16px
        line-height: 1.25em;  // 20px
        letter-spacing: 0.5px;
        margin-top: 16px;
    }

    // .slider__dots

    &__dots {
        margin-top: 50px;
    }

    // .slider__dots-row

    &__dots-row {
        display: flex;
        gap: 15px;
    }

    // .slider__dot

    &__dot {
        width: 18px;
        height: 18px;
        border: 2px solid #1C1C1D;
        border-radius: 50%;

        &--active {
            background-color: #FFBD12;
        }
    }
}