.start {
    background-color: #962EFF;
    padding: 60px 0;
    // .start__row

    &__row {
        display: flex;
        justify-content: space-around;
        align-items: center;
        gap: 30px;

    }

    // .start__content

    &__content {
        flex-direction: column;
        max-width: 483px;
    }

    // .start__title

    &__title {
        color: #fff;
        margin-bottom: 0.55555555555em; //25px
    }

    // .start__text

    &__text {
        color: #fff;
        margin-bottom: 2.77777777778em; //50px
    }

    // .start__from

    &__form {
        display: flex;
        width: 100%;
    }

    // .start__input

    &__input {
        width: 100%;
        margin-right: 10px;
    }

    // .start__btn

    &__btn {
        font-weight: 900;
        font-size: 1rem; // 16px
        line-height: 1em; //16px
        text-align: center;
        letter-spacing: 0.45px;
        padding: 14px 50px;
        color: #FFFFFF;
        background-color: #FFBD12;
        white-space: nowrap;
    }

    // .start__img

    &__img {
        object-fit: contain;
        max-width: 100%;
        height: 100%;
    }
}

@media (max-width: 1400px) {
    .start {

        &__content {
            width: 50%;
        }
        &__img {
            width: 50%;
        }
    }   
}

@media (max-width: 1000px) {
    .start {
        &__row {
            flex-direction: column;
            gap: 0;
        }

        &__content {
            order: 2;
            width: 100%;
            max-width: 70%;
        }
        
        &__img {
            order: 1;
            width: 70%;
        }
    }   
}

@media (max-width: 760px) {
    .start {
        &__content {
            max-width: 90%;
        }

        &__img {
            width: 90%;
        }
    }   
}

@media (max-width: 500px) {
    .start {
        &__content {
            max-width: 100%;
        }

        &__img {
            width: 100%;
        }

        &__btn {
            padding: 7px 20px;
        }
    }   
}

@media (max-width: 400px) {
    .start {
        &__form {
            flex-direction: column;
        }

        &__input {
            margin-right: 0;
            margin-bottom: 10px;
        }

        &__btn {
            width: 120px;
            height: 50px;
        }

    }   
}